import { ref, reactive, readonly, computed } from 'vue'

// 
const state = reactive({
  // 
  user: {
    name: '',
    email: '',
    avatar: '',
    preferences: {
      language: 'zh-CN',
      timezone: 'Asia/Shanghai',
      notifications: true
    }
  },
  
  // 
  app: {
    title: 'Vue3 ',
    version: '1.0.0',
    theme: 'light',
    loading: false,
    online: navigator.onLine,
    sidebarCollapsed: false
  },
  
  // 
  notifications: [],
  
  // 
  modals: {
    themeSelector: false,
    userProfile: false,
    settings: false
  },
  
  // 
  stats: {
    pageViews: 0,
    userActions: 0,
    sessionStart: Date.now(),
    lastActivity: Date.now()
  }
})

// 
const _internal = {
  notificationId: 0,
  activityTimer: null
}

/**
 *  Store
 *  Vue 3 Composition API 
 */
export function useAppStore() {
  
  // ==========  ==========
  
  const isLoggedIn = computed(() => {
    return !!(state.user.name && state.user.email)
  })
  
  const sessionDuration = computed(() => {
    return Date.now() - state.stats.sessionStart
  })
  
  const unreadNotifications = computed(() => {
    return state.notifications.filter(n => !n.read).length
  })
  
  const isOnline = computed(() => state.app.online)
  
  const currentUser = computed(() => ({
    ...state.user,
    isLoggedIn: isLoggedIn.value
  }))
  
  // ==========  ==========
  
  const setUser = (userData) => {
    Object.assign(state.user, userData)
    updateActivity()
  }
  
  const updateUserPreferences = (preferences) => {
    Object.assign(state.user.preferences, preferences)
    updateActivity()
  }
  
  const clearUser = () => {
    state.user.name = ''
    state.user.email = ''
    state.user.avatar = ''
    state.user.preferences = {
      language: 'zh-CN',
      timezone: 'Asia/Shanghai',
      notifications: true
    }
    updateActivity()
  }
  
  // ==========  ==========
  
  const setTheme = (theme) => {
    state.app.theme = theme
    updateActivity()
  }
  
  const setLoading = (loading) => {
    state.app.loading = loading
  }
  
  const toggleSidebar = () => {
    state.app.sidebarCollapsed = !state.app.sidebarCollapsed
    updateActivity()
  }
  
  const setOnlineStatus = (online) => {
    state.app.online = online
  }
  
  // ==========  ==========
  
  const addNotification = (notification) => {
    const id = ++_internal.notificationId
    const newNotification = {
      id,
      type: 'info',
      title: '',
      message: '',
      duration: 5000,
      read: false,
      timestamp: Date.now(),
      ...notification
    }
    
    state.notifications.unshift(newNotification)
    
    // 
    if (newNotification.duration > 0) {
      setTimeout(() => {
        removeNotification(id)
      }, newNotification.duration)
    }
    
    updateActivity()
    return id
  }
  
  const removeNotification = (id) => {
    const index = state.notifications.findIndex(n => n.id === id)
    if (index > -1) {
      state.notifications.splice(index, 1)
    }
  }
  
  const markNotificationAsRead = (id) => {
    const notification = state.notifications.find(n => n.id === id)
    if (notification) {
      notification.read = true
      updateActivity()
    }
  }
  
  const clearAllNotifications = () => {
    state.notifications.length = 0
    updateActivity()
  }
  
  const clearReadNotifications = () => {
    state.notifications = state.notifications.filter(n => !n.read)
    updateActivity()
  }
  
  // ==========  ==========
  
  const openModal = (modalName) => {
    if (modalName in state.modals) {
      state.modals[modalName] = true
      updateActivity()
    }
  }
  
  const closeModal = (modalName) => {
    if (modalName in state.modals) {
      state.modals[modalName] = false
      updateActivity()
    }
  }
  
  const closeAllModals = () => {
    Object.keys(state.modals).forEach(key => {
      state.modals[key] = false
    })
    updateActivity()
  }
  
  // ==========  ==========
  
  const incrementPageViews = () => {
    state.stats.pageViews++
    updateActivity()
  }
  
  const incrementUserActions = () => {
    state.stats.userActions++
    updateActivity()
  }
  
  const updateActivity = () => {
    state.stats.lastActivity = Date.now()
    
    // 
    if (_internal.activityTimer) {
      clearTimeout(_internal.activityTimer)
    }
    
    // 
    _internal.activityTimer = setTimeout(() => {
      // 
      console.log('')
    }, 30 * 60 * 1000) // 30
  }
  
  const resetStats = () => {
    state.stats = {
      pageViews: 0,
      userActions: 0,
      sessionStart: Date.now(),
      lastActivity: Date.now()
    }
  }
  
  // ==========  ==========
  
  const saveToLocalStorage = () => {
    try {
      const dataToSave = {
        user: state.user,
        app: {
          theme: state.app.theme,
          sidebarCollapsed: state.app.sidebarCollapsed
        },
        stats: state.stats
      }
      localStorage.setItem('app-state', JSON.stringify(dataToSave))
    } catch (error) {
      console.warn(':', error)
    }
  }
  
  const loadFromLocalStorage = () => {
    try {
      const savedData = localStorage.getItem('app-state')
      if (savedData) {
        const parsedData = JSON.parse(savedData)
        
        // 
        if (parsedData.user) {
          Object.assign(state.user, parsedData.user)
        }
        
        // 
        if (parsedData.app) {
          Object.assign(state.app, parsedData.app)
        }
        
        // 
        if (parsedData.stats) {
          Object.assign(state.stats, parsedData.stats)
        }
      }
    } catch (error) {
      console.warn(':', error)
    }
  }
  
  const clearLocalStorage = () => {
    try {
      localStorage.removeItem('app-state')
    } catch (error) {
      console.warn(':', error)
    }
  }
  
  // ==========  ==========
  
  const initialize = () => {
    // 
    loadFromLocalStorage()
    
    // 
    const handleOnline = () => setOnlineStatus(true)
    const handleOffline = () => setOnlineStatus(false)
    
    window.addEventListener('online', handleOnline)
    window.addEventListener('offline', handleOffline)
    
    // 
    const handleVisibilityChange = () => {
      if (document.visibilityState === 'visible') {
        updateActivity()
      }
    }
    
    document.addEventListener('visibilitychange', handleVisibilityChange)
    
    // 
    const saveInterval = setInterval(saveToLocalStorage, 30000) // 30
    
    // 
    const handleBeforeUnload = () => {
      saveToLocalStorage()
    }
    
    window.addEventListener('beforeunload', handleBeforeUnload)
    
    // 
    return () => {
      window.removeEventListener('online', handleOnline)
      window.removeEventListener('offline', handleOffline)
      document.removeEventListener('visibilitychange', handleVisibilityChange)
      window.removeEventListener('beforeunload', handleBeforeUnload)
      clearInterval(saveInterval)
      if (_internal.activityTimer) {
        clearTimeout(_internal.activityTimer)
      }
    }
  }
  
  // ==========  ==========
  
  const getDebugInfo = () => {
    return {
      state: JSON.parse(JSON.stringify(state)),
      computed: {
        isLoggedIn: isLoggedIn.value,
        sessionDuration: sessionDuration.value,
        unreadNotifications: unreadNotifications.value,
        isOnline: isOnline.value
      },
      internal: {
        notificationId: _internal.notificationId
      }
    }
  }
  
  // ========== API ==========
  
  return {
    // 
    state: readonly(state),
    
    // 
    isLoggedIn,
    sessionDuration,
    unreadNotifications,
    isOnline,
    currentUser,
    
    // 
    setUser,
    updateUserPreferences,
    clearUser,
    
    // 
    setTheme,
    setLoading,
    toggleSidebar,
    setOnlineStatus,
    
    // 
    addNotification,
    removeNotification,
    markNotificationAsRead,
    clearAllNotifications,
    clearReadNotifications,
    
    // 
    openModal,
    closeModal,
    closeAllModals,
    
    // 
    incrementPageViews,
    incrementUserActions,
    updateActivity,
    resetStats,
    
    // 
    saveToLocalStorage,
    loadFromLocalStorage,
    clearLocalStorage,
    
    // 
    initialize,
    
    // 
    getDebugInfo
  }
}

// 
let globalStore = null

/**
 * 
 * 
 */
export function getGlobalAppStore() {
  if (!globalStore) {
    globalStore = useAppStore()
  }
  return globalStore
}