<template>
  <div class="contact-page">
    <!--  -->
    <Header 
      title="" 
      subtitle=""
    />

    <div class="contact-content">
      <!--  -->
      <Card title="" :shadow="true" class="contact-form-card">
        <template #default>
          <form @submit.prevent="handleSubmit" class="contact-form" v-if="!showSuccess">
            <div class="form-row">
              <div class="form-group">
                <label for="name" class="form-label"> *</label>
                <input
                  id="name"
                  v-model="form.name"
                  type="text"
                  class="form-input"
                  :class="{ 'form-input--error': errors.name }"
                  placeholder=""
                />
                <span v-if="errors.name" class="error-message">{{ errors.name }}</span>
              </div>

              <div class="form-group">
                <label for="email" class="form-label"> *</label>
                <input
                  id="email"
                  v-model="form.email"
                  type="email"
                  class="form-input"
                  :class="{ 'form-input--error': errors.email }"
                  placeholder=""
                />
                <span v-if="errors.email" class="error-message">{{ errors.email }}</span>
              </div>
            </div>

            <div class="form-group">
              <label for="subject" class="form-label"></label>
              <input
                id="subject"
                v-model="form.subject"
                type="text"
                class="form-input"
                placeholder=""
              />
            </div>

            <div class="form-group">
              <label for="message" class="form-label"> *</label>
              <textarea
                id="message"
                v-model="form.message"
                class="form-textarea"
                :class="{ 'form-input--error': errors.message }"
                rows="5"
                placeholder=""
              ></textarea>
              <span v-if="errors.message" class="error-message">{{ errors.message }}</span>
              <div class="character-count">
                {{ form.message.length }}/500 
              </div>
            </div>

            <div class="form-actions">
              <Button 
                type="primary" 
                size="medium"
                :disabled="isSubmitting"
                @click="handleSubmit"
              >
                {{ isSubmitting ? '...' : '' }}
              </Button>
              <Button 
                type="secondary" 
                size="medium"
                @click="resetForm"
                :disabled="isSubmitting"
              >
                
              </Button>
            </div>
          </form>

          <!--  -->
          <div v-if="showSuccess" class="success-message">
            <div class="success-icon"></div>
            <h3></h3>
            <p></p>
            <Button 
              type="primary" 
              size="small"
              @click="resetSuccess"
              class="success-button"
            >
              
            </Button>
          </div>
        </template>
      </Card>

      <!--  -->
      <div class="contact-info-grid">
        <Card title="" :shadow="true">
          <template #default>
            <div class="contact-methods">
              <div class="contact-method" v-for="method in contactMethods" :key="method.type">
                <div class="method-icon">{{ method.icon }}</div>
                <div class="method-content">
                  <h4 class="method-title">{{ method.title }}</h4>
                  <p class="method-value">{{ method.value }}</p>
                  <span class="method-description">{{ method.description }}</span>
                </div>
              </div>
            </div>
          </template>
        </Card>

        <Card title="" :shadow="true">
          <template #default>
            <div class="working-hours">
              <div class="hours-item" v-for="schedule in workingHours" :key="schedule.day">
                <span class="hours-day">{{ schedule.day }}</span>
                <span class="hours-time" :class="{ 'hours-time--closed': schedule.closed }">
                  {{ schedule.closed ? '' : schedule.time }}
                </span>
              </div>
            </div>
            <div class="timezone-info">
              <p>  (UTC+8)</p>
              <p> support@vue3app.com</p>
            </div>
          </template>
        </Card>

        <Card title="" :shadow="true">
          <template #default>
            <div class="faq-section">
              <div 
                class="faq-item" 
                v-for="(faq, index) in faqs" 
                :key="index"
                @click="toggleFaq(index)"
                :class="{ 'faq-item--open': openFaqIndex === index }"
              >
                <div class="faq-question">
                  <span>{{ faq.question }}</span>
                  <span class="faq-toggle">{{ openFaqIndex === index ? '' : '+' }}</span>
                </div>
                <div class="faq-answer" v-if="openFaqIndex === index">
                  {{ faq.answer }}
                </div>
              </div>
            </div>
          </template>
          <template #actions>
            <Button type="secondary" size="small" @click="viewAllFaqs">
              FAQ
            </Button>
          </template>
        </Card>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, reactive, computed } from 'vue'
import Header from '../components/common/Header.vue'
import Card from '../components/ui/Card.vue'
import Button from '../components/ui/Button.vue'

export default {
  name: 'Contact',
  components: {
    Header,
    Card,
    Button
  },
  setup() {
    // 
    const form = reactive({
      name: '',
      email: '',
      subject: '',
      message: ''
    })

    // 
    const errors = reactive({
      name: '',
      email: '',
      message: ''
    })

    // 
    const isSubmitting = ref(false)
    const showSuccess = ref(false)
    const openFaqIndex = ref(null)

    // 
    const contactMethods = ref([
      {
        type: 'email',
        icon: '',
        title: '',
        value: 'contact@vue3app.com',
        description: '24'
      },
      {
        type: 'website',
        icon: '',
        title: '',
        value: 'www.vue3app.com',
        description: ''
      },
      {
        type: 'github',
        icon: '',
        title: 'GitHub',
        value: 'github.com/vue3app',
        description: ''
      },
      {
        type: 'support',
        icon: '',
        title: '',
        value: 'support@vue3app.com',
        description: ''
      }
    ])

    // 
    const workingHours = ref([
      { day: '', time: '09:00 - 18:00', closed: false },
      { day: '', time: '09:00 - 18:00', closed: false },
      { day: '', time: '09:00 - 18:00', closed: false },
      { day: '', time: '09:00 - 18:00', closed: false },
      { day: '', time: '09:00 - 18:00', closed: false },
      { day: '', time: '', closed: true },
      { day: '', time: '', closed: true }
    ])

    // 
    const faqs = ref([
      {
        question: 'Vue3',
        answer: 'GitHubnpm installnpm run dev'
      },
      {
        question: '',
        answer: 'ChromeFirefoxSafariEdge'
      },
      {
        question: '',
        answer: 'MIT'
      },
      {
        question: '',
        answer: 'Pull Request'
      }
    ])

    // 
    const validateForm = () => {
      // 
      errors.name = ''
      errors.email = ''
      errors.message = ''

      let isValid = true

      // 
      if (!form.name.trim()) {
        errors.name = ''
        isValid = false
      } else if (form.name.trim().length < 2) {
        errors.name = '2'
        isValid = false
      }

      // 
      if (!form.email.trim()) {
        errors.email = ''
        isValid = false
      } else if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(form.email)) {
        errors.email = ''
        isValid = false
      }

      // 
      if (!form.message.trim()) {
        errors.message = ''
        isValid = false
      } else if (form.message.trim().length < 10) {
        errors.message = '10'
        isValid = false
      } else if (form.message.length > 500) {
        errors.message = '500'
        isValid = false
      }

      return isValid
    }

    // 
    const handleSubmit = async () => {
      if (!validateForm()) {
        return
      }

      isSubmitting.value = true

      try {
        // API
        await new Promise(resolve => setTimeout(resolve, 2000))
        
        // 
        showSuccess.value = true
        
        // 
        resetForm()
        
      } catch (error) {
        console.error(':', error)
        alert('')
      } finally {
        isSubmitting.value = false
      }
    }

    // 
    const resetForm = () => {
      form.name = ''
      form.email = ''
      form.subject = ''
      form.message = ''
      
      // 
      errors.name = ''
      errors.email = ''
      errors.message = ''
    }

    // 
    const resetSuccess = () => {
      showSuccess.value = false
    }

    // FAQ
    const toggleFaq = (index) => {
      openFaqIndex.value = openFaqIndex.value === index ? null : index
    }

    // FAQ
    const viewAllFaqs = () => {
      alert('FAQ')
    }

    return {
      // 
      form,
      errors,
      isSubmitting,
      showSuccess,
      
      // 
      contactMethods,
      workingHours,
      faqs,
      openFaqIndex,
      
      // 
      handleSubmit,
      resetForm,
      resetSuccess,
      toggleFaq,
      viewAllFaqs
    }
  }
}
</script>

<style scoped>
.contact-page {
  min-height: 100vh;
}

.contact-content {
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: var(--spacing-2xl);
  align-items: start;
  padding: var(--spacing-2xl) 0;
}

/*  */
.contact-form-card {
  height: fit-content;
}

.contact-form {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-lg);
}

.form-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: var(--spacing-lg);
}

.form-group {
  display: flex;
  flex-direction: column;
}

.form-label {
  font-weight: 500;
  color: var(--color-text-primary);
  margin-bottom: var(--spacing-sm);
  font-size: var(--font-size-sm);
}

.form-input,
.form-textarea {
  padding: var(--spacing-md);
  border: 1px solid var(--color-border);
  border-radius: var(--border-radius-md);
  font-size: var(--font-size-base);
  background-color: var(--color-bg-secondary);
  color: var(--color-text-primary);
  transition: all var(--transition-fast);
  font-family: inherit;
}

.form-input:focus,
.form-textarea:focus {
  outline: none;
  border-color: var(--color-primary);
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}

.form-input--error {
  border-color: var(--color-danger);
}

.form-textarea {
  resize: vertical;
  min-height: 120px;
}

.error-message {
  color: var(--color-danger);
  font-size: var(--font-size-sm);
  margin-top: var(--spacing-xs);
}

.character-count {
  font-size: var(--font-size-xs);
  color: var(--color-text-muted);
  text-align: right;
  margin-top: var(--spacing-xs);
}

.form-actions {
  display: flex;
  gap: var(--spacing-md);
  margin-top: var(--spacing-md);
}

/*  */
.success-message {
  text-align: center;
  padding: var(--spacing-2xl);
}

.success-icon {
  font-size: 4rem;
  margin-bottom: var(--spacing-lg);
}

.success-message h3 {
  color: var(--color-success);
  margin-bottom: var(--spacing-md);
  font-weight: 600;
  font-size: var(--font-size-xl);
}

.success-message p {
  color: var(--color-text-secondary);
  margin-bottom: var(--spacing-lg);
  line-height: 1.6;
}

.success-button {
  margin-top: var(--spacing-md);
}

/*  */
.contact-info-grid {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-xl);
}

/*  */
.contact-methods {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-md);
}

.contact-method {
  display: flex;
  align-items: flex-start;
  gap: var(--spacing-md);
  padding: var(--spacing-md);
  border-radius: var(--border-radius-md);
  background-color: var(--color-bg-tertiary);
  transition: all var(--transition-fast);
}

.contact-method:hover {
  background-color: var(--color-bg-secondary);
  transform: translateY(-2px);
  box-shadow: var(--shadow-sm);
}

.method-icon {
  font-size: var(--font-size-xl);
  flex-shrink: 0;
  margin-top: var(--spacing-xs);
}

.method-content {
  flex: 1;
}

.method-title {
  font-size: var(--font-size-base);
  font-weight: 600;
  color: var(--color-text-primary);
  margin: 0 0 var(--spacing-xs) 0;
}

.method-value {
  font-size: var(--font-size-sm);
  color: var(--color-primary);
  font-weight: 500;
  margin: 0 0 var(--spacing-xs) 0;
}

.method-description {
  font-size: var(--font-size-xs);
  color: var(--color-text-muted);
}

/*  */
.working-hours {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-sm);
  margin-bottom: var(--spacing-lg);
}

.hours-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: var(--spacing-sm) var(--spacing-md);
  background-color: var(--color-bg-tertiary);
  border-radius: var(--border-radius-sm);
}

.hours-day {
  font-size: var(--font-size-sm);
  font-weight: 500;
  color: var(--color-text-primary);
}

.hours-time {
  font-size: var(--font-size-sm);
  color: var(--color-success);
  font-weight: 500;
}

.hours-time--closed {
  color: var(--color-text-muted);
}

.timezone-info {
  padding-top: var(--spacing-md);
  border-top: 1px solid var(--color-border);
}

.timezone-info p {
  font-size: var(--font-size-xs);
  color: var(--color-text-secondary);
  margin: var(--spacing-xs) 0;
  line-height: 1.5;
}

/* FAQ */
.faq-section {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-sm);
}

.faq-item {
  border: 1px solid var(--color-border);
  border-radius: var(--border-radius-md);
  overflow: hidden;
  transition: all var(--transition-fast);
  cursor: pointer;
}

.faq-item:hover {
  border-color: var(--color-primary);
}

.faq-item--open {
  border-color: var(--color-primary);
  box-shadow: var(--shadow-sm);
}

.faq-question {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: var(--spacing-md);
  background-color: var(--color-bg-tertiary);
  font-size: var(--font-size-sm);
  font-weight: 500;
  color: var(--color-text-primary);
}

.faq-toggle {
  font-size: var(--font-size-lg);
  font-weight: 700;
  color: var(--color-primary);
  transition: transform var(--transition-fast);
}

.faq-item--open .faq-toggle {
  transform: rotate(180deg);
}

.faq-answer {
  padding: var(--spacing-md);
  background-color: var(--color-bg-primary);
  font-size: var(--font-size-sm);
  color: var(--color-text-secondary);
  line-height: 1.6;
  border-top: 1px solid var(--color-border);
}

/*  */
@media (max-width: 768px) {
  .contact-content {
    grid-template-columns: 1fr;
    gap: var(--spacing-xl);
    padding: var(--spacing-xl) 0;
  }
  
  .form-row {
    grid-template-columns: 1fr;
    gap: var(--spacing-md);
  }
  
  .form-actions {
    flex-direction: column;
  }
  
  .contact-method {
    padding: var(--spacing-sm);
  }
  
  .method-icon {
    font-size: var(--font-size-lg);
  }
  
  .hours-item {
    padding: var(--spacing-xs) var(--spacing-sm);
  }
  
  .faq-question {
    padding: var(--spacing-sm);
  }
  
  .faq-answer {
    padding: var(--spacing-sm);
  }
}

@media (max-width: 480px) {
  .success-message {
    padding: var(--spacing-lg);
  }
  
  .success-icon {
    font-size: 3rem;
  }
  
  .contact-method {
    flex-direction: column;
    text-align: center;
    gap: var(--spacing-sm);
  }
  
  .method-icon {
    align-self: center;
  }
}
</style>