import { createRouter, createWebHistory } from 'vue-router'
import { getGlobalAppStore } from '../stores/app.js'

// 
import Home from '../views/Home.vue'
import About from '../views/About.vue'
import Contact from '../views/Contact.vue'

// 
const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    meta: {
      title: '',
      description: 'Vue3Vue3',
      keywords: 'Vue3, , , ',
      requiresAuth: false,
      keepAlive: true,
      transition: 'fade',
      icon: ''
    }
  },
  {
    path: '/about',
    name: 'About',
    component: About,
    meta: {
      title: '',
      description: 'Vue3',
      keywords: 'Vue3, , , ',
      requiresAuth: false,
      keepAlive: true,
      transition: 'slide-left',
      icon: ''
    }
  },
  {
    path: '/contact',
    name: 'Contact',
    component: Contact,
    meta: {
      title: '',
      description: '',
      keywords: 'Vue3, , , ',
      requiresAuth: false,
      keepAlive: false,
      transition: 'slide-right',
      icon: ''
    }
  },
  // 404
  {
    path: '/:pathMatch(.*)*',
    name: 'NotFound',
    component: () => import('../views/NotFound.vue'),
    meta: {
      title: '',
      description: '',
      requiresAuth: false,
      keepAlive: false,
      transition: 'fade'
    }
  }
]

// 
const router = createRouter({
  history: createWebHistory(import.meta.env.BASE_URL),
  routes,
  // 
  scrollBehavior(to, from, savedPosition) {
    // /
    if (savedPosition) {
      return savedPosition
    }
    
    // 
    if (to.hash) {
      return {
        el: to.hash,
        behavior: 'smooth'
      }
    }
    
    // 
    return { 
      top: 0,
      behavior: 'smooth'
    }
  }
})

// 
let loadingTimer = null

// 
router.beforeEach(async (to, from, next) => {
  try {
    // 
    const store = getGlobalAppStore()
    
    // 
    if (loadingTimer) {
      clearTimeout(loadingTimer)
    }
    
    // 
    loadingTimer = setTimeout(() => {
      store.setLoading(true)
    }, 200)
    
    // 
    store.incrementPageViews()
    
    // 
    if (to.meta.requiresAuth) {
      if (!store.isLoggedIn.value) {
        // 
        next({ name: 'Login', query: { redirect: to.fullPath } })
        return
      }
    }
    
    // 
    if (to.meta.title) {
      document.title = `${to.meta.title} - Vue3 `
    } else {
      document.title = 'Vue3 '
    }
    
    // 
    setPageMeta(to.meta)
    
    // 
    if (from.name && to.name !== from.name) {
      store.addNotification({
        type: 'info',
        title: '',
        message: `${to.meta.title || to.name}`,
        duration: 1500
      })
    }
    
    // 
    if (import.meta.env.DEV) {
      console.log(` : ${from.path} -> ${to.path}`)
      console.log(':', to.meta)
    }
    
    next()
  } catch (error) {
    console.error(':', error)
    next(false) // 
  }
})

// 
router.beforeResolve(async (to, from, next) => {
  // 
  try {
    // 
    if (to.meta.preload) {
      // 
      await preloadRouteData(to)
    }
    
    next()
  } catch (error) {
    console.error(':', error)
    next(false)
  }
})

// 
router.afterEach((to, from, failure) => {
  // 
  if (loadingTimer) {
    clearTimeout(loadingTimer)
    loadingTimer = null
  }
  
  const store = getGlobalAppStore()
  store.setLoading(false)
  
  // 
  if (failure) {
    console.error(':', failure)
    store.addNotification({
      type: 'error',
      title: '',
      message: '',
      duration: 3000
    })
    return
  }
  
  // 
  if (import.meta.env.DEV) {
    console.log(` : ${to.path}`)
  }
  
  // 
  store.updateActivity()
  
  // 
  if (window.performance && window.performance.mark) {
    window.performance.mark(`route-${to.name}-loaded`)
  }
})

// 
router.onError((error) => {
  console.error(' :', error)
  
  // 
  if (loadingTimer) {
    clearTimeout(loadingTimer)
    loadingTimer = null
  }
  
  const store = getGlobalAppStore()
  store.setLoading(false)
  
  // 
  store.addNotification({
    type: 'error',
    title: '',
    message: '',
    duration: 5000
  })
  
  // 
  if (import.meta.env.DEV) {
    console.error(':', error.stack)
  }
  
  // 
  if (import.meta.env.PROD) {
    // 
    reportError(error, 'router')
  }
})

// 
function setPageMeta(meta) {
  // 
  if (meta.description) {
    let metaDescription = document.querySelector('meta[name="description"]')
    if (!metaDescription) {
      metaDescription = document.createElement('meta')
      metaDescription.name = 'description'
      document.head.appendChild(metaDescription)
    }
    metaDescription.content = meta.description
  }
  
  // 
  if (meta.keywords) {
    let metaKeywords = document.querySelector('meta[name="keywords"]')
    if (!metaKeywords) {
      metaKeywords = document.createElement('meta')
      metaKeywords.name = 'keywords'
      document.head.appendChild(metaKeywords)
    }
    metaKeywords.content = meta.keywords
  }
  
  // Open Graph
  if (meta.title) {
    let ogTitle = document.querySelector('meta[property="og:title"]')
    if (!ogTitle) {
      ogTitle = document.createElement('meta')
      ogTitle.setAttribute('property', 'og:title')
      document.head.appendChild(ogTitle)
    }
    ogTitle.content = meta.title
  }
  
  if (meta.description) {
    let ogDescription = document.querySelector('meta[property="og:description"]')
    if (!ogDescription) {
      ogDescription = document.createElement('meta')
      ogDescription.setAttribute('property', 'og:description')
      document.head.appendChild(ogDescription)
    }
    ogDescription.content = meta.description
  }
}

// 
async function preloadRouteData(to) {
  // 
  console.log(':', to.name)
  // 
  await new Promise(resolve => setTimeout(resolve, 100))
}

// 
function reportError(error, context) {
  // Sentry
  console.log(':', { error, context })
}

export default router