import { ref, readonly, computed, watch, onMounted } from 'vue'

// 
const theme = ref('light')
const isInitialized = ref(false)

/**
 * 
 * @returns {object} 
 */
export function useTheme() {
  // 
  const isDark = computed(() => theme.value === 'dark')
  const isLight = computed(() => theme.value === 'light')
  
  // 
  const themes = [
    'light', 
    'dark', 
    'blue', 
    'green', 
    'purple', 
    'red', 
    'orange', 
    'pink', 
    'cyan',
    'high-contrast',
    'high-contrast-dark',
    'eye-care'
  ]
  
  // 
  const setTheme = (newTheme) => {
    if (themes.includes(newTheme)) {
      theme.value = newTheme
      applyTheme(newTheme)
      saveTheme(newTheme)
    }
  }

  const toggleTheme = () => {
    const newTheme = theme.value === 'light' ? 'dark' : 'light'
    setTheme(newTheme)
  }

  const initTheme = () => {
    if (isInitialized.value) return

    // localStorage
    const savedTheme = getSavedTheme()
    
    // 
    if (!savedTheme) {
      const systemTheme = getSystemTheme()
      setTheme(systemTheme)
    } else {
      setTheme(savedTheme)
    }

    // 
    watchSystemTheme()
    
    isInitialized.value = true
  }

  // DOM
  const applyTheme = (themeName) => {
    document.documentElement.setAttribute('data-theme', themeName)
    
    // metatheme-color
    updateThemeColor(themeName)
  }

  // localStorage
  const saveTheme = (themeName) => {
    try {
      localStorage.setItem('theme', themeName)
    } catch (error) {
      console.warn(':', error)
    }
  }

  // localStorage
  const getSavedTheme = () => {
    try {
      return localStorage.getItem('theme')
    } catch (error) {
      console.warn(':', error)
      return null
    }
  }

  // 
  const getSystemTheme = () => {
    if (typeof window !== 'undefined' && window.matchMedia) {
      return window.matchMedia('(prefers-color-scheme: dark)').matches ? 'dark' : 'light'
    }
    return 'light'
  }

  // 
  const watchSystemTheme = () => {
    if (typeof window !== 'undefined' && window.matchMedia) {
      const mediaQuery = window.matchMedia('(prefers-color-scheme: dark)')
      
      const handleChange = (e) => {
        // 
        const savedTheme = getSavedTheme()
        if (!savedTheme) {
          const systemTheme = e.matches ? 'dark' : 'light'
          setTheme(systemTheme)
        }
      }

      // addEventListener
      if (mediaQuery.addEventListener) {
        mediaQuery.addEventListener('change', handleChange)
      } else {
        // addListener
        mediaQuery.addListener(handleChange)
      }
    }
  }

  // meta
  const updateThemeColor = (themeName) => {
    const themeColors = {
      light: '#ffffff',
      dark: '#111827'
    }

    let metaThemeColor = document.querySelector('meta[name="theme-color"]')
    if (!metaThemeColor) {
      metaThemeColor = document.createElement('meta')
      metaThemeColor.name = 'theme-color'
      document.head.appendChild(metaThemeColor)
    }
    
    metaThemeColor.content = themeColors[themeName] || themeColors.light
  }

  // CSS
  const getThemeVariable = (variableName) => {
    if (typeof window !== 'undefined') {
      return getComputedStyle(document.documentElement)
        .getPropertyValue(`--${variableName}`)
        .trim()
    }
    return ''
  }

  // CSS
  const setThemeVariable = (variableName, value) => {
    if (typeof window !== 'undefined') {
      document.documentElement.style.setProperty(`--${variableName}`, value)
    }
  }

  return {
    // 
    theme: readonly(theme),
    isDark,
    isLight,
    themes,
    
    // 
    setTheme,
    toggleTheme,
    initTheme,
    getThemeVariable,
    setThemeVariable
  }
}

/**
 * 
 * 
 */
export function useThemeWithPreferences() {
  const { theme, isDark, isLight, setTheme, toggleTheme, initTheme } = useTheme()
  
  // 
  const preferences = ref({
    followSystem: false,
    autoSwitch: false,
    switchTime: {
      dark: '18:00',
      light: '06:00'
    }
  })

  // 
  const loadPreferences = () => {
    try {
      const saved = localStorage.getItem('theme-preferences')
      if (saved) {
        Object.assign(preferences.value, JSON.parse(saved))
      }
    } catch (error) {
      console.warn(':', error)
    }
  }

  // 
  const savePreferences = () => {
    try {
      localStorage.setItem('theme-preferences', JSON.stringify(preferences.value))
    } catch (error) {
      console.warn(':', error)
    }
  }

  // 
  const checkAutoSwitch = () => {
    if (!preferences.value.autoSwitch) return

    const now = new Date()
    const currentTime = `${now.getHours().toString().padStart(2, '0')}:${now.getMinutes().toString().padStart(2, '0')}`
    
    const { dark: darkTime, light: lightTime } = preferences.value.switchTime
    
    if (currentTime >= darkTime || currentTime < lightTime) {
      if (theme.value !== 'dark') {
        setTheme('dark')
      }
    } else {
      if (theme.value !== 'light') {
        setTheme('light')
      }
    }
  }

  // 
  watch(preferences, savePreferences, { deep: true })

  // 
  onMounted(() => {
    loadPreferences()
    
    if (preferences.value.autoSwitch) {
      checkAutoSwitch()
      // 
      setInterval(checkAutoSwitch, 60000)
    }
  })

  return {
    theme,
    isDark,
    isLight,
    preferences: readonly(preferences),
    setTheme,
    toggleTheme,
    initTheme,
    loadPreferences,
    savePreferences
  }
}