<template>
  <div class="home-page">
    <!--  -->
    <Header 
      title="Vue3" 
      subtitle="Vue3"
    />

    <!--  -->
    <section class="demo-section">
      <div class="demo-grid">
        <!--  -->
        <Card title="" :shadow="true">
          <template #default>
            <p class="card-description">Vue3Composition API</p>
            <Counter 
              label=""
              :initial-value="0"
              :show-info="true"
              :show-double="true"
              @update="handleCounterUpdate"
            />
          </template>
        </Card>

        <!--  -->
        <Card title="" :shadow="true">
          <template #default>
            <p class="card-description"></p>
            <Counter 
              label=": 0-10"
              :initial-value="5"
              :min="0"
              :max="10"
              :bounded="true"
              :show-step-controls="true"
              :show-quick-actions="false"
            />
          </template>
        </Card>

        <!--  -->
        <Card title="" :shadow="true">
          <template #default>
            <p class="card-description">
              : <strong class="theme-indicator">{{ themeDisplayName }}</strong>
            </p>
            <div class="theme-controls">
              <Button 
                type="primary" 
                size="medium"
                @click="toggleTheme"
              >
                {{ themeButtonText }}
              </Button>
              <div class="theme-info">
                <span class="theme-status">{{ themeStatus }}</span>
              </div>
            </div>
          </template>
        </Card>

        <!-- Vue3 -->
        <Card title="Vue3 " :shadow="true">
          <template #default>
            <p class="card-description">Vue3</p>
            <div class="feature-grid">
              <div class="feature-item">
                <span class="feature-icon"></span>
                <div class="feature-content">
                  <h4>Composition API</h4>
                  <p></p>
                </div>
              </div>
              <div class="feature-item">
                <span class="feature-icon"></span>
                <div class="feature-content">
                  <h4></h4>
                  <p></p>
                </div>
              </div>
              <div class="feature-item">
                <span class="feature-icon"></span>
                <div class="feature-content">
                  <h4></h4>
                  <p>UI</p>
                </div>
              </div>
              <div class="feature-item">
                <span class="feature-icon"></span>
                <div class="feature-content">
                  <h4></h4>
                  <p></p>
                </div>
              </div>
            </div>
          </template>
          <template #actions>
            <Button type="secondary" size="small" @click="showMoreFeatures">
              
            </Button>
          </template>
        </Card>

        <!--  -->
        <Card title="" :shadow="true">
          <template #default>
            <p class="card-description"></p>
            <div class="stats-grid">
              <div class="stat-item">
                <span class="stat-number">{{ stats.counterClicks }}</span>
                <span class="stat-label"></span>
              </div>
              <div class="stat-item">
                <span class="stat-number">{{ stats.themeToggles }}</span>
                <span class="stat-label"></span>
              </div>
              <div class="stat-item">
                <span class="stat-number">{{ stats.pageViews }}</span>
                <span class="stat-label"></span>
              </div>
            </div>
          </template>
          <template #actions>
            <Button type="danger" size="small" @click="resetStats">
              
            </Button>
          </template>
        </Card>
      </div>
    </section>
  </div>
</template>

<script>
import { ref, computed, onMounted, watch } from 'vue'
import { useTheme } from '../composables/useTheme.js'
import Header from '../components/common/Header.vue'
import Card from '../components/ui/Card.vue'
import Counter from '../components/ui/Counter.vue'
import Button from '../components/ui/Button.vue'

export default {
  name: 'Home',
  components: {
    Header,
    Card,
    Counter,
    Button
  },
  setup() {
    // 
    const { theme, isDark, toggleTheme, initTheme } = useTheme()

    // 
    const stats = ref({
      counterClicks: 0,
      themeToggles: 0,
      pageViews: 1
    })

    // 
    const themeDisplayName = computed(() => {
      return isDark.value ? '' : ''
    })

    const themeButtonText = computed(() => {
      return `${isDark.value ? '' : ''}`
    })

    const themeStatus = computed(() => {
      return isDark.value ? ' ' : ' '
    })

    // 
    const handleCounterUpdate = (value) => {
      stats.value.counterClicks++
    }

    const handleThemeToggle = () => {
      toggleTheme()
      stats.value.themeToggles++
    }

    const showMoreFeatures = () => {
      // 
      alert('')
    }

    const resetStats = () => {
      stats.value = {
        counterClicks: 0,
        themeToggles: 0,
        pageViews: stats.value.pageViews
      }
    }

    // 
    onMounted(() => {
      initTheme()
      
      // localStorage
      const savedStats = localStorage.getItem('home-stats')
      if (savedStats) {
        try {
          const parsed = JSON.parse(savedStats)
          stats.value = {
            ...parsed,
            pageViews: parsed.pageViews + 1
          }
        } catch (error) {
          console.warn(':', error)
        }
      }

      // 
      const saveStats = () => {
        localStorage.setItem('home-stats', JSON.stringify(stats.value))
      }

      // watchstats
      watch(stats, saveStats, { deep: true })
    })

    return {
      // 
      theme,
      isDark,
      themeDisplayName,
      themeButtonText,
      themeStatus,
      toggleTheme: handleThemeToggle,
      
      // 
      stats,
      
      // 
      handleCounterUpdate,
      showMoreFeatures,
      resetStats
    }
  }
}
</script>

<style scoped>
.home-page {
  min-height: 100vh;
}

/*  */
.demo-section {
  padding: var(--spacing-2xl) 0;
}

.demo-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: var(--spacing-xl);
}

/*  */
.card-description {
  color: var(--color-text-secondary);
  margin-bottom: var(--spacing-lg);
  line-height: 1.6;
  font-size: var(--font-size-sm);
}

/*  */
.theme-controls {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-md);
  align-items: center;
}

.theme-indicator {
  color: var(--color-primary);
  font-weight: 600;
}

.theme-info {
  text-align: center;
}

.theme-status {
  font-size: var(--font-size-sm);
  color: var(--color-text-muted);
  padding: var(--spacing-xs) var(--spacing-sm);
  background-color: var(--color-bg-tertiary);
  border-radius: var(--border-radius-sm);
}

/*  */
.feature-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: var(--spacing-md);
  margin-top: var(--spacing-md);
}

.feature-item {
  display: flex;
  align-items: flex-start;
  gap: var(--spacing-sm);
  padding: var(--spacing-sm);
  border-radius: var(--border-radius-md);
  transition: background-color var(--transition-fast);
}

.feature-item:hover {
  background-color: var(--color-bg-tertiary);
}

.feature-icon {
  font-size: var(--font-size-xl);
  flex-shrink: 0;
  margin-top: var(--spacing-xs);
}

.feature-content h4 {
  margin: 0 0 var(--spacing-xs) 0;
  font-size: var(--font-size-sm);
  font-weight: 600;
  color: var(--color-text-primary);
}

.feature-content p {
  margin: 0;
  font-size: var(--font-size-xs);
  color: var(--color-text-secondary);
  line-height: 1.4;
}

/*  */
.stats-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: var(--spacing-md);
  margin-top: var(--spacing-md);
}

.stat-item {
  text-align: center;
  padding: var(--spacing-md);
  background-color: var(--color-bg-tertiary);
  border-radius: var(--border-radius-md);
  transition: all var(--transition-fast);
}

.stat-item:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-sm);
}

.stat-number {
  display: block;
  font-size: var(--font-size-2xl);
  font-weight: 700;
  color: var(--color-primary);
  margin-bottom: var(--spacing-xs);
}

.stat-label {
  font-size: var(--font-size-xs);
  color: var(--color-text-secondary);
  font-weight: 500;
}

/*  */
@media (max-width: 768px) {
  .demo-grid {
    grid-template-columns: 1fr;
    gap: var(--spacing-lg);
  }
  
  .feature-grid {
    grid-template-columns: 1fr;
  }
  
  .stats-grid {
    grid-template-columns: 1fr;
    gap: var(--spacing-sm);
  }
  
  .feature-item {
    padding: var(--spacing-xs);
  }
}

@media (max-width: 480px) {
  .demo-section {
    padding: var(--spacing-xl) 0;
  }
  
  .theme-controls {
    gap: var(--spacing-sm);
  }
  
  .stat-item {
    padding: var(--spacing-sm);
  }
  
  .stat-number {
    font-size: var(--font-size-xl);
  }
}
</style>