import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import './assets/styles/main.css'
import { getGlobalAppStore } from './stores/app.js'

// Vue
const app = createApp(App)

// 
app.use(router)

// 
const globalStore = getGlobalAppStore()
const cleanupStore = globalStore.initialize()

// 
app.config.errorHandler = (err, vm, info) => {
  console.error('Vue:', err)
  console.error(':', info)
  console.error(':', vm)
  
  // 
  globalStore.addNotification({
    type: 'error',
    title: 'Vue',
    message: err.message || '',
    duration: 5000
  })
  
  // 
  if (import.meta.env.DEV) {
    console.error(':', err.stack)
  }
}

// 
app.config.warnHandler = (msg, vm, trace) => {
  console.warn('Vue:', msg)
  console.warn(':', trace)
  
  // 
  if (import.meta.env.DEV) {
    globalStore.addNotification({
      type: 'warning',
      title: 'Vue',
      message: msg,
      duration: 3000
    })
  }
}

// 
app.config.globalProperties.$appName = 'Vue3 '
app.config.globalProperties.$version = '1.0.0'
app.config.globalProperties.$store = globalStore

// 
if (import.meta.env.DEV) {
  app.config.performance = true
}

//  - 
router.beforeEach((to, from, next) => {
  // 
  globalStore.incrementPageViews()
  
  // 
  if (to.meta.title) {
    document.title = `${to.meta.title} - Vue3 `
  } else {
    document.title = 'Vue3 '
  }
  
  // 
  if (to.meta.description) {
    const metaDescription = document.querySelector('meta[name="description"]')
    if (metaDescription) {
      metaDescription.setAttribute('content', to.meta.description)
    }
  }
  
  // 
  if (import.meta.env.DEV) {
    console.log(` : ${from.path} -> ${to.path}`)
  }
  
  next()
})

// 
app.mount('#app')

// 
window.addEventListener('beforeunload', () => {
  globalStore.saveToLocalStorage()
  if (cleanupStore) {
    cleanupStore()
  }
})

// 
if (import.meta.env.DEV) {
  console.log(' Vue3')
  console.log(' :', app.config.globalProperties.$version)
  console.log(' ')
  console.log(' ')
  
  // storewindow
  window.__APP_STORE__ = globalStore
}