<template>
  <div class="about-page">
    <!--  -->
    <Header 
      title="" 
      subtitle="Vue3"
    />

    <div class="content-grid">
      <!--  -->
      <Card title="" :shadow="true">
        <template #default>
          <div class="project-intro">
            <p class="intro-text">
              Vue3Vue3 Composition API
              Vite
            </p>
            <p class="intro-text">
              Vue3
              Vue3
            </p>
            <div class="project-stats">
              <div class="stat-item">
                <span class="stat-number">{{ projectStats.components }}</span>
                <span class="stat-label"></span>
              </div>
              <div class="stat-item">
                <span class="stat-number">{{ projectStats.pages }}</span>
                <span class="stat-label"></span>
              </div>
              <div class="stat-item">
                <span class="stat-number">{{ projectStats.features }}</span>
                <span class="stat-label"></span>
              </div>
            </div>
          </div>
        </template>
        <template #actions>
          <Button type="primary" size="small" @click="viewSource">
            
          </Button>
        </template>
      </Card>

      <!--  -->
      <Card title="" :shadow="true">
        <template #default>
          <div class="tech-stack">
            <div class="tech-category" v-for="category in techStack" :key="category.name">
              <h3 class="tech-category-title">{{ category.name }}</h3>
              <div class="tech-items">
                <div 
                  class="tech-item" 
                  v-for="tech in category.items" 
                  :key="tech.name"
                >
                  <div class="tech-icon">{{ tech.icon }}</div>
                  <div class="tech-info">
                    <h4 class="tech-name">{{ tech.name }}</h4>
                    <p class="tech-description">{{ tech.description }}</p>
                    <span class="tech-version">{{ tech.version }}</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </template>
      </Card>

      <!--  -->
      <Card title="" :shadow="true">
        <template #default>
          <div class="features-grid">
            <div 
              class="feature-item" 
              v-for="feature in coreFeatures" 
              :key="feature.title"
              @click="selectFeature(feature)"
              :class="{ 'feature-item--selected': selectedFeature?.title === feature.title }"
            >
              <div class="feature-icon">{{ feature.icon }}</div>
              <h3 class="feature-title">{{ feature.title }}</h3>
              <p class="feature-description">{{ feature.description }}</p>
              <div class="feature-tags">
                <span 
                  class="feature-tag" 
                  v-for="tag in feature.tags" 
                  :key="tag"
                >
                  {{ tag }}
                </span>
              </div>
            </div>
          </div>
          
          <!--  -->
          <div v-if="selectedFeature" class="feature-details">
            <h4>{{ selectedFeature.title }} - </h4>
            <p>{{ selectedFeature.details }}</p>
            <div class="feature-examples" v-if="selectedFeature.examples">
              <h5></h5>
              <ul>
                <li v-for="example in selectedFeature.examples" :key="example">
                  {{ example }}
                </li>
              </ul>
            </div>
          </div>
        </template>
        <template #actions>
          <Button 
            type="secondary" 
            size="small" 
            @click="clearSelection"
            v-if="selectedFeature"
          >
            
          </Button>
        </template>
      </Card>

      <!--  -->
      <Card title="" :shadow="true">
        <template #default>
          <div class="team-info">
            <div class="team-section">
              <h4></h4>
              <div class="status-grid">
                <div class="status-item">
                  <span class="status-label"></span>
                  <span class="status-value">v1.0.0</span>
                </div>
                <div class="status-item">
                  <span class="status-label"></span>
                  <span class="status-value status-success"> </span>
                </div>
                <div class="status-item">
                  <span class="status-label"></span>
                  <span class="status-value">{{ lastUpdated }}</span>
                </div>
              </div>
            </div>
            
            <div class="team-section">
              <h4></h4>
              <div class="env-info">
                <p><strong>Node.js:</strong> {{ envInfo.node }}</p>
                <p><strong>:</strong> {{ envInfo.packageManager }}</p>
                <p><strong>:</strong> {{ envInfo.buildTool }}</p>
              </div>
            </div>
          </div>
        </template>
        <template #actions>
          <Button type="secondary" size="small" @click="checkUpdates">
            
          </Button>
        </template>
      </Card>
    </div>
  </div>
</template>

<script>
import { ref, computed, onMounted } from 'vue'
import Header from '../components/common/Header.vue'
import Card from '../components/ui/Card.vue'
import Button from '../components/ui/Button.vue'

export default {
  name: 'About',
  components: {
    Header,
    Card,
    Button
  },
  setup() {
    // 
    const selectedFeature = ref(null)
    
    // 
    const projectStats = ref({
      components: 8,
      pages: 3,
      features: 6
    })

    // 
    const techStack = ref([
      {
        name: '',
        items: [
          {
            name: 'Vue 3',
            icon: '',
            description: 'JavaScript',
            version: 'v3.4.0'
          },
          {
            name: 'Composition API',
            icon: '',
            description: 'API',
            version: 'Built-in'
          },
          {
            name: 'Vue Router',
            icon: '',
            description: '',
            version: 'v4.2.5'
          }
        ]
      },
      {
        name: '',
        items: [
          {
            name: 'Vite',
            icon: '',
            description: '',
            version: 'v5.0.0'
          },
          {
            name: 'ES6+',
            icon: '',
            description: 'JavaScript',
            version: 'Latest'
          },
          {
            name: 'CSS3',
            icon: '',
            description: '',
            version: 'Latest'
          }
        ]
      },
      {
        name: '',
        items: [
          {
            name: 'Vitest',
            icon: '',
            description: '',
            version: 'v1.0.0'
          },
          {
            name: 'Vue Test Utils',
            icon: '',
            description: 'Vue',
            version: 'v2.4.0'
          }
        ]
      }
    ])

    // 
    const coreFeatures = ref([
      {
        title: '',
        icon: '',
        description: 'Vue3 Composition API',
        tags: ['Vue3', 'Composition API', ''],
        details: 'Vue3Composition APITypeScript',
        examples: [
          'setup()',
          'composables',
          'TypeScript'
        ]
      },
      {
        title: '',
        icon: '',
        description: 'Vite',
        tags: ['Vite', '', ''],
        details: 'ViteES',
        examples: [
          '',
          '',
          'ES'
        ]
      },
      {
        title: '',
        icon: '',
        description: '',
        tags: ['', '', 'CSS Grid'],
        details: 'CSS GridFlexbox',
        examples: [
          'CSS Grid',
          '',
          ''
        ]
      },
      {
        title: '',
        icon: '',
        description: '',
        tags: ['', '', 'CSS'],
        details: 'CSS',
        examples: [
          'CSS',
          '',
          ''
        ]
      },
      {
        title: '',
        icon: '',
        description: 'UI',
        tags: ['', '', ''],
        details: 'API',
        examples: [
          'UI',
          'Props',
          ''
        ]
      },
      {
        title: '',
        icon: '',
        description: 'Composition API',
        tags: ['', 'Composables', ''],
        details: 'Vue3Composition API',
        examples: [
          'useCounter',
          'useTheme',
          ''
        ]
      }
    ])

    // 
    const envInfo = ref({
      node: 'v18.0.0+',
      packageManager: 'npm',
      buildTool: 'Vite 5.0'
    })

    // 
    const lastUpdated = computed(() => {
      return new Date().toLocaleDateString('zh-CN')
    })

    // 
    const selectFeature = (feature) => {
      selectedFeature.value = selectedFeature.value?.title === feature.title ? null : feature
    }

    const clearSelection = () => {
      selectedFeature.value = null
    }

    const viewSource = () => {
      window.open('https://github.com/your-repo/vue3-frontend-app', '_blank')
    }

    const checkUpdates = () => {
      alert('')
    }

    return {
      // 
      selectedFeature,
      projectStats,
      techStack,
      coreFeatures,
      envInfo,
      
      // 
      lastUpdated,
      
      // 
      selectFeature,
      clearSelection,
      viewSource,
      checkUpdates
    }
  }
}
</script>

<style scoped>
.about-page {
  min-height: 100vh;
}

.content-grid {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-2xl);
  padding: var(--spacing-2xl) 0;
}

/*  */
.project-intro {
  text-align: center;
}

.intro-text {
  color: var(--color-text-secondary);
  line-height: 1.7;
  margin-bottom: var(--spacing-lg);
  font-size: var(--font-size-base);
}

.project-stats {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: var(--spacing-lg);
  margin-top: var(--spacing-xl);
  padding-top: var(--spacing-lg);
  border-top: 1px solid var(--color-border);
}

.stat-item {
  text-align: center;
  padding: var(--spacing-md);
  background-color: var(--color-bg-tertiary);
  border-radius: var(--border-radius-md);
  transition: all var(--transition-fast);
}

.stat-item:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-sm);
}

.stat-number {
  display: block;
  font-size: var(--font-size-2xl);
  font-weight: 700;
  color: var(--color-primary);
  margin-bottom: var(--spacing-xs);
}

.stat-label {
  font-size: var(--font-size-sm);
  color: var(--color-text-secondary);
  font-weight: 500;
}

/*  */
.tech-stack {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-xl);
}

.tech-category-title {
  font-size: var(--font-size-lg);
  color: var(--color-primary);
  margin-bottom: var(--spacing-md);
  font-weight: 600;
  border-bottom: 2px solid var(--color-primary);
  padding-bottom: var(--spacing-xs);
}

.tech-items {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: var(--spacing-md);
}

.tech-item {
  display: flex;
  align-items: flex-start;
  gap: var(--spacing-md);
  padding: var(--spacing-md);
  background-color: var(--color-bg-tertiary);
  border-radius: var(--border-radius-md);
  transition: all var(--transition-fast);
}

.tech-item:hover {
  background-color: var(--color-bg-secondary);
  transform: translateY(-2px);
  box-shadow: var(--shadow-sm);
}

.tech-icon {
  font-size: var(--font-size-xl);
  flex-shrink: 0;
  margin-top: var(--spacing-xs);
}

.tech-info {
  flex: 1;
}

.tech-name {
  font-size: var(--font-size-base);
  font-weight: 600;
  color: var(--color-text-primary);
  margin: 0 0 var(--spacing-xs) 0;
}

.tech-description {
  font-size: var(--font-size-sm);
  color: var(--color-text-secondary);
  margin: 0 0 var(--spacing-xs) 0;
  line-height: 1.5;
}

.tech-version {
  font-size: var(--font-size-xs);
  color: var(--color-text-muted);
  background-color: var(--color-bg-primary);
  padding: var(--spacing-xs) var(--spacing-sm);
  border-radius: var(--border-radius-sm);
  font-weight: 500;
}

/*  */
.features-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: var(--spacing-lg);
  margin-bottom: var(--spacing-xl);
}

.feature-item {
  padding: var(--spacing-lg);
  border-radius: var(--border-radius-md);
  background-color: var(--color-bg-tertiary);
  transition: all var(--transition-normal);
  cursor: pointer;
  border: 2px solid transparent;
}

.feature-item:hover {
  transform: translateY(-4px);
  box-shadow: var(--shadow-md);
  border-color: var(--color-primary);
}

.feature-item--selected {
  border-color: var(--color-primary);
  background-color: var(--color-bg-secondary);
}

.feature-icon {
  font-size: var(--font-size-3xl);
  margin-bottom: var(--spacing-md);
  text-align: center;
}

.feature-title {
  font-size: var(--font-size-lg);
  color: var(--color-text-primary);
  margin-bottom: var(--spacing-sm);
  font-weight: 600;
  text-align: center;
}

.feature-description {
  color: var(--color-text-secondary);
  font-size: var(--font-size-sm);
  line-height: 1.6;
  margin-bottom: var(--spacing-md);
  text-align: center;
}

.feature-tags {
  display: flex;
  flex-wrap: wrap;
  gap: var(--spacing-xs);
  justify-content: center;
}

.feature-tag {
  background-color: var(--color-primary);
  color: var(--color-white);
  padding: var(--spacing-xs) var(--spacing-sm);
  border-radius: var(--border-radius-sm);
  font-size: var(--font-size-xs);
  font-weight: 500;
}

/*  */
.feature-details {
  margin-top: var(--spacing-xl);
  padding: var(--spacing-lg);
  background-color: var(--color-bg-secondary);
  border-radius: var(--border-radius-md);
  border-left: 4px solid var(--color-primary);
}

.feature-details h4 {
  color: var(--color-primary);
  margin-bottom: var(--spacing-md);
  font-size: var(--font-size-lg);
}

.feature-details p {
  color: var(--color-text-secondary);
  line-height: 1.6;
  margin-bottom: var(--spacing-md);
}

.feature-examples h5 {
  color: var(--color-text-primary);
  margin-bottom: var(--spacing-sm);
  font-size: var(--font-size-base);
}

.feature-examples ul {
  margin: 0;
  padding-left: var(--spacing-lg);
}

.feature-examples li {
  color: var(--color-text-secondary);
  margin-bottom: var(--spacing-xs);
  line-height: 1.5;
}

/*  */
.team-info {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: var(--spacing-xl);
}

.team-section h4 {
  color: var(--color-primary);
  margin-bottom: var(--spacing-md);
  font-size: var(--font-size-lg);
  font-weight: 600;
}

.status-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: var(--spacing-sm);
}

.status-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: var(--spacing-sm) var(--spacing-md);
  background-color: var(--color-bg-tertiary);
  border-radius: var(--border-radius-sm);
}

.status-label {
  font-size: var(--font-size-sm);
  color: var(--color-text-secondary);
  font-weight: 500;
}

.status-value {
  font-size: var(--font-size-sm);
  color: var(--color-text-primary);
  font-weight: 600;
}

.status-success {
  color: var(--color-success) !important;
}

.env-info p {
  margin: var(--spacing-sm) 0;
  color: var(--color-text-secondary);
  font-size: var(--font-size-sm);
  line-height: 1.5;
}

.env-info strong {
  color: var(--color-text-primary);
}

/*  */
@media (max-width: 768px) {
  .content-grid {
    padding: var(--spacing-xl) 0;
    gap: var(--spacing-xl);
  }
  
  .project-stats {
    grid-template-columns: 1fr;
    gap: var(--spacing-md);
  }
  
  .tech-items {
    grid-template-columns: 1fr;
  }
  
  .features-grid {
    grid-template-columns: 1fr;
    gap: var(--spacing-md);
  }
  
  .team-info {
    grid-template-columns: 1fr;
    gap: var(--spacing-lg);
  }
  
  .feature-item {
    padding: var(--spacing-md);
  }
  
  .feature-details {
    padding: var(--spacing-md);
  }
}

@media (max-width: 480px) {
  .stat-item {
    padding: var(--spacing-sm);
  }
  
  .stat-number {
    font-size: var(--font-size-xl);
  }
  
  .tech-item {
    flex-direction: column;
    text-align: center;
    gap: var(--spacing-sm);
  }
  
  .feature-tags {
    justify-content: flex-start;
  }
}
</style>